footer{
    /* margin-top: 10vmax; */
    padding: 2vmax;
    background-color: rgb(114, 94, 94);
    color: white;
    display: flex;
    align-items: center;
  }
  
  .leftFooter{
      width: 20%;
      display: flex;
      flex-direction: column;
      align-items: center;
  }
  
  .leftFooter> h4{
      font-family: "Roboto";
      font-size: 1vmax;
  }
  .leftFooter >p{
      text-align: center;
      font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
      font-size: 1.2vmax;
  } 
  .leftFooter > img{
      width: 10vmax;
      margin: 1vmax;
      cursor: pointer;
  }
  
  .midFooter{
      width: 60%;
      text-align: center;
  }
  
  .midFooter > img{

      width: 20vmax;
  }
  .midFooter > p{
      max-width: 60%;
      margin: 1vmax auto;
  }
  
  .rightFooter{
      width: 20%;
      display: flex;
      flex-direction: column;
      align-items: center;
  }
  
  .rightFooter > h4{
      font-family:"Roboto";
      font-size: 1.4vmax;
      text-decoration: underline;
  }
  
  .rightFooter >a{
      text-decoration: none;
      font-size: 1.3vmax;
      font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
      color: white;
      transition: all 0.3s;
      margin: 0.5vmax;
  }
  
  .rightFooter >a:hover{
      color:#59b7c3
  }
  
  