.loading{
    width:100vw;
    height: 100vh;
    background-color: white;
    display: grid;
    place-items: center;
    max-width: 100%;
}

.loading>div{
    width: 10vmax;
    height: 10vmax;
    border-bottom: 5px solid rgba(19, 19, 19, 0.639);
    border-radius: 50%;
    animation: loadingRotate 800ms linear infinite;
}
@keyframes loadingRotate{
    to{
        transform: rotateZ(-360deg);
    }
} 
